<template>
  <div class="register">
    <span class="dl_fc" :fixed="true" @click="goPage('Login')">登录</span><span class="gbc_r" :fixed="true" @click="goPage('Menu')">关闭</span>
      <div class="box-text">请先注册</div>
      <div class="logo">
          <img class="auto-img" src="../assets/logo.png" alt="">
      </div>
    <van-form @submit="register">
      <van-field class="box-dz"
        v-model="userInfo.phone"
        name="手机号"
        label="手机号"
        placeholder="请输入手机号"
        
      />
       <van-field class="box-dz"
        v-model="userInfo.nickName"
        type="text"
        name="昵称"
        label="昵称"
        placeholder="请输入您的昵称"
      />
      <van-field class="box-dz"
        v-model="userInfo.password"
        type="password"
        name="密码"
        label="密码"
        placeholder="请输入密码：8-16字符"
      />
      <div class="btn-box">
        <van-button class="btn-cor" color="#D79F52" round block type="info" native-type="submit">注册</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import {valid} from '../utils/valid'

//导入公共方法模块
import {utils} from '../utils/utils'

export default {
  name: "Register",

  computed: {
    userInfo() {
      return this.$store.state.registerModule.userInfo;
    }
  },

  methods: {
    //注册
    register() {
      

      if (valid.validForm(this.userInfo, this)) {

        //发起ajax请求

        //加载提示
        this.$toast.loading({
          
          //是否禁止背景点击
          forbidClick: true,

          //禁止自动关闭
          duration: 0,

          message: '注册中...'

        })

        this.axios({
          method: 'POST',
          url: '/register',

          //请求体参数
          data: {
            appkey: this.appkey,
            nickName: this.userInfo.nickName,
            phone: this.userInfo.phone,
            password: this.userInfo.password
          },

          //序列换请求体参数
          transformRequest: utils.transformRequest

        }).then(result => {
          
          

          //关闭加载提示
          this.$toast.clear();

          //如果注册成功
          if (result.data.code == 100) {
            //跳转登录组件
            this.$router.push({name: 'Login'});
          } else {
            this.$toast({
              message: result.data.msg
            })
          }

        }).catch(err => {
          
          this.$toast.clear();
        })

      }

      
    },

    goPage(routerName) {
      this.$router.push({name: routerName});
    }
  }
};
</script>

<style lang="less" scoped>

.register { 
    background: #3B2212;
    height: 100%;
  .btn-box {
    margin-top: 16px;
    padding: 35px 20px;
    clear: both;

  }
  .logo{
      margin: 50px auto;
      width: 140px;
      height: 140px;
      border-radius:50%;
      border: 1px solid #ddd;
      background: white;
     
  }
  .box-text{
      font-size: 30px;
      text-align: center;
      padding-top: 80px;
      color:white;
  }
  .box-dz{
      margin: 0 auto;
      margin-bottom: 20px !important;
      width: 90% !important;
  }
  .dl_fc{
    color: #ffffff;
    font-size: 15px;
    display: inline-block;
    height: 44px;
    float: left;
    line-height: 44px;
    margin-left: 10px;
  }
  .gbc_r{
    color: #ffffff;
    font-size: 15px;
    display: inline-block;
    height: 44px;
    float: right;
    line-height: 44px;
    margin-right: 10px;
   
  }
}
</style>